.dialogs {
    margin: 30px 100px;
    display: grid;
    grid-template-columns: 2fr 10fr;
    box-shadow: 0px 0 15px -10px #333;
    border-radius: 10px;
}
.dialogsItems {
    border-top-right-radius: 7px;
    border-bottom-right-radius: 7px;
    border-right: 2px solid #cecece;
    z-index: 1;
}
.dialogsItems a{
    border-top-right-radius: 7px;
    border-bottom-right-radius: 7px;
    padding: 10px;
    color: #797F76;
    display: block;

}
a.active {
    color: #50B5FF;
    background-color: #F5FBFF;
}

.messages {
    padding: 10px;
}
