a {
    text-decoration: none;
}
.app-wrapper {
    margin: 0 auto;
    display: grid;
    height: 100vh;
    grid-template-areas:
            "h h"
            "n c";
    grid-template-rows: 60px 1fr;
    grid-auto-columns: 2fr 10fr;
}
.app-wrapper-content {
    grid-area: c;
    background-color: #FAFAFB;
}

