.nav {
    grid-area: n;
    background-color: #ffffff;
    padding: 20px;
    box-shadow: 1px 0 10px -8px #333;
    z-index: 1;
}

.item a {
    padding: 10px;
    display: block;
    color: #797F76;
}
.item a:hover {
    color: #50B5FF;
    transition: 0.3s;
}
.item a.active {
    color: #50B5FF;
    background-color: #F5FBFF;
}
